































































import { Component, Vue } from 'vue-property-decorator';
import { FarmItem } from '@/types';

@Component({
  name: 'User',
})
export default class User extends Vue {
  private farmItem: FarmItem = {
    FARM_ID: '',
  };

  private breadItem = [
    {
      text: 'Dashboard',
      disabled: false,
      href: 'breadcrumbs_dashboard',
    },
    {
      text: 'Link 1',
      disabled: false,
      href: 'breadcrumbs_link_1',
    },
    {
      text: 'Link 2',
      disabled: true,
      href: 'breadcrumbs_link_2',
    },
  ]

  created() {
    this.farmItem.FARM_ID = this.$route.params.farm;
    console.log('user created', this.farmItem);
  }
}
